.list{margin-bottom:30px}.list__add-button{opacity:0.4}.list__add-button:hover{opacity:1}.list__remove-icon{opacity:0}.list li{display:flex;align-items:center;cursor:pointer;padding:10px 12px;transition:background-color 0.15s ease-in-out}.list li:hover .list__remove-icon{opacity:0.2}.list li .list__remove-icon{opacity:0;transition:opacity 0.15s ease-in-out}.list li .list__remove-icon:hover{opacity:0.8}.list li span{flex:1 1}.list li:hover{background:rgba(255,255,255,0.5)}.list li.active{background:#FFFFFF;box-shadow:0px 3px 10px rgba(0,3,0,0.04);border-radius:4px}.list li .badge{position:relative;left:5px}.list li i{display:inline-flex;margin-right:8px}.list li i svg path{fill:#7c7c7c}

.badge{display:inline-block;width:10px;height:10px;border-radius:30px}.badge--grey{background:#C9D1D3}.badge--lime{background:#B6E6BD}.badge--purple{background:#C355F5}.badge--black{background:#08001A}.badge--green{background-color:#42b883}.badge--blue{background-color:#64c4ed}.badge--pink{background-color:#ffbbcc}

.add-list__popup{height:150px;background:#FFFFFF;box-shadow:0px 6px 10px rgba(0,0,0,0.05);border-radius:10px;padding:18px;box-sizing:border-box;position:relative}.add-list__popup .button{width:100%;margin-top:12px}.add-list__popup-close-btn{position:absolute;right:-8px;top:-8px;cursor:pointer}.add-list__popup-colors{display:flex;justify-content:space-between;margin-top:12px}.add-list__popup-colors .badge{width:16px;height:16px;cursor:pointer;border:2px solid transparent}.add-list__popup-colors .badge.active{border:2px solid #525252}

.tasks__title{font-family:'Sriracha', cursive;font-weight:bold;font-size:32px;line-height:39px;color:#64c4ed;border-bottom:1px solid #f2f2f2;padding-bottom:20px}.tasks__title:hover img{opacity:0.2}.tasks__title img{opacity:0;cursor:pointer;margin-left:15px}.tasks__title img:hover{opacity:0.8}.tasks__items{margin-top:30px}.tasks__items-row{display:flex;align-items:center}.tasks__items-row input{margin-left:15px;border:0;font-size:16px;width:100%}.tasks .checkbox input{display:none}.tasks .checkbox svg{transition:background-color 0.15s ease-in-out}.tasks .checkbox svg path{stroke:#f2f2f2}.tasks .checkbox input:checked+label{background:#4DD599;border-color:#4DD599}.tasks .checkbox input:checked+label svg{opacity:1}.tasks .checkbox input:checked+label svg path{stroke:#fff}.tasks .checkbox:hover label{background-color:#f2f2f2;border-color:#f2f2f2}.tasks .checkbox:hover label svg{opacity:1}.tasks .checkbox:hover label svg path{stroke:#b2b2b2}.tasks .checkbox label{display:flex;align-items:center;justify-content:center;border:2px solid #e8e8e8;border-radius:30px;width:20px;height:20px;cursor:pointer;transition:background-color 0.15s ease-in-out, border-color 0.15s ease-in-out}.tasks .checkbox label svg{opacity:0}

*{margin:0;padding:0;list-style:none;outline:none;font-family:'Roboto', sistem-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-size:14px}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.todo{display:flex;position:absolute;left:50%;top:50%;width:750px;height:530px;transform:translate(-50%, -50%);box-shadow:1px 2px 20px #f3f3f3;border-radius:10px;border:1px solid #f1f1f1}.todo__sidebar{background-color:#f4f6f8;width:200px;height:calc(100% - 120px);border-right:1px solid #f1f1f1;padding:60px 20px}.todo__tasks{flex:1 1;padding:60px}.button{background:#4DD599;border-radius:4px;color:#FFFFFF;border:0;padding:10px 20px;cursor:pointer}.button:hover{background:#34cf8b}.button:active{background:#2cb97b}.field{background:#FFFFFF;border:1px solid #efefef;box-sizing:border-box;border-radius:4px;padding:8px 12px;width:100%}.field:focus{border-color:#dbdbdb}

